import React, {Component} from 'react'
import styles from './style.module.css';


export default class ExcelAbout extends Component {
    
    render() {
        return(
                <div className={styles["excel-details"]}>
                        <p className={styles["about-text"]}>Being the nation's second and South Asia's first ever fest of its kind, not only has Excel consistently kept up with its rich legacy but has also made it a point to kick up a notch every subsequent year. The Tech Fest provides one of the most prominent platforms for the country's most intelligent minds to gather and exchange ideas. It has brought out numerous talents and inventions to spotlight over the years and is now back with its 21st edition assured to be full of revolutionary ideas as well as technological advancements. </p>
                </div>
        );
    }
}


